<template>
  <section class="page-card">
    <h1>Card</h1>
    <div class="cont">
      <vb-card>
        <vb-card-header>
          <vb-card-header-title>This is card title</vb-card-header-title>
        </vb-card-header>
        <vb-card-image>
          <vb-image
            ratio="4:3"
            src="https://bulma.io/images/placeholders/1280x960.png"
          ></vb-image>
        </vb-card-image>
        <vb-card-content>
          <div class="media">
            <div class="media-left">
              <figure class="image is-48x48">
                <img
                  src="https://bulma.io/images/placeholders/96x96.png"
                  alt="Placeholder image"
                />
              </figure>
            </div>
            <div class="media-content">
              <p class="title is-4">John Smith</p>
              <p class="subtitle is-6">@johnsmith</p>
            </div>
          </div>

          <p>
            
            
          </p>
          <p>
            
            &ldquo;&rdquo;
          </p>
        </vb-card-content>
        <vb-card-footer>
          <vb-card-footer-item>
            <a href="#">Save</a>
          </vb-card-footer-item>
          <vb-card-footer-item>
            <a href="#">Edit</a>
          </vb-card-footer-item>
        </vb-card-footer>
      </vb-card>
    </div>
  </section>
</template>

<script>
export default {
  name: 'page-card'
};
</script>

<style lang="scss">
.cont {
  width: 500px;
  margin: 0 auto;
}
</style>



