<template>
  <section class="page-box">
    <vb-panel>
      <vb-panel-heading>repositories</vb-panel-heading>
      <vb-panel-tabs
        :items="items"
        :activeIndex.sync="activeIndex"
      ></vb-panel-tabs>
      <vb-panel-block>
        <vb-panel-icon iconClass="ion ion-ios-alert" />
        
        
      </vb-panel-block>
    </vb-panel>
  </section>
</template>

<script>
export default {
  name: 'page-box',
  data() {
    return {
      activeIndex: 0,
      items: ['all', 'public', 'private']
    };
  }
};
</script>



